<template>

  <div class="container">

<!--    <el-button @click="lock = !lock"> Lock </el-button>-->

    <!--    Content-->
    <transition name="el-zoom-in-top">

      <div v-show="!lock">

        <el-row type="flex" justify="end">
          <el-col :span="12" align="right">

            <el-select v-model="timeReverse" placeholder="" class="mt-5">
              <el-option
                  v-for="item in options"
                  :key="item.label"
                  :label="item.label"
                  :value="item.value">
              </el-option>
            </el-select>

          </el-col>
        </el-row>


        <h3 style="text-align: left; font-style: italic; font-family: " class="my-1">To :</h3>

        <el-divider></el-divider>

        <el-timeline :reverse="this.timeReverse">

          <el-timeline-item
              v-for="mail in mails"
              :timestamp="mail.time"
              :key="mail.time"
              placement="top">

            <MailMessage
                v-for="(msg, index) in mail.messages"
                :key="index"
                :on-left="msg.onLeft"
                :is-small-width="isSmallWidth"
                :content="msg.content">

            </MailMessage>

          </el-timeline-item>

        </el-timeline>
      </div>

    </transition>

<!--    Login-->
    <div class="container mt-5" v-if="lock">
      <el-row>
        <el-col :span="12" :offset="6">
          <el-image
              :src="require('@/assets/img/mail-logo.jpg')">
          </el-image>
        </el-col>
      </el-row>

      <el-row>
        <el-col :span="16" :offset="4">

          <el-form :model="form" :inline="true">
            <el-form-item label="Password" >
              <el-input v-model="form.password" placeholder="8-number pin" ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit" plain>Open</el-button>
            </el-form-item>
          </el-form>

        </el-col>
      </el-row>
    </div>

  </div>

</template>

<script>

import MailMessage from "@/components/Mail/MailMessage";

export default {
  name: "ToGuoguo",
  components: {MailMessage},

  data(){
    return{
      lock: true,
      isSmallWidth: false,
      timeReverse: true,

      options:[
        {value: false, label:""},
        {value: true, label:""}
      ],

      form:{
        password: ''
      },

      mails: [
        // ----------------------------------------- 2/12
        {
          time: "2021 / 02 / 12",
          messages: [
            {
              onLeft: true,
              content: "" +
                  "\n\n" +
                  ""

            },
            {
              onLeft: false,
              content: ""
            },
            {
              onLeft: true,
              content: "What the heck? "
            },
            {
              onLeft: false,
              content: "       ~ \n\n" +
                  ""
            },
            {
              onLeft: true,
              content: "Cross"
            },
            {
              onLeft: false,
              content: ""
            },
            {
              onLeft: true,
              content: "~"
            },
            {
              onLeft: false,
              content: ""
            }
          ]
        },
        // ----------------------------------------- 3/27
        {
          time: "2021 / 04 / 10",
          messages: [
            {
              onLeft: true,
              content: "Dear \n" +
                  "    \n" +
                  "    22\n" +
                  "    \n" +
                  "    \n" +
                  "    \n" +
                  "    \n" +
                  "    offer\n" +
                  "    \n" +
                  "    \n" +
                  "    \n"
            },
            {
              onLeft: false,
              content: ""
            },
            {
              onLeft: true,
              content: ""
            },
            {
              onLeft: false,
              content: ""
            },
            {
              onLeft: true,
              content: ""
            },
            {
              onLeft: false,
              content: ""
            },
            {
              onLeft: true,
              content: ""
            },
            {
              onLeft: false,
              content: ""
            },
            {
              onLeft: true,
              content: ""
            },
            {
              onLeft: false,
              content: ""
            }
          ]
        },
        {
          time: "2025 / 11 / 10",
          messages: [
            {
              onLeft: true,
              content: "This is a test" +
                  "\n\n" +
                  ""

            },
          ]
        },
      ]
    }
  },

  methods: {
    onSubmit() {
      console.log(this.form.password);
      const currDate = new Date();

      let year = currDate.getFullYear();
      let month = currDate.getMonth() + 1;
      let date = currDate.getDate();

      if (month < 10){
        month = '0' + month;
      }

      if (date < 10){
        date = '0' + date;
      }

      let psw = '' + year + month + date;

      if (psw == this.form.password){
        this.lock = false;
        this.$notify({
          title: "Welcome back, ",
          message: "",
          iconClass: "el-icon-loading"
        })
      }
      else{
        this.$message.error({
          message: "Wrong Password!",
          duration: 1500
        });
      }
    },

    checkWidthSize: function ()
    {
      //console.log('get Card Width')

      let windowWidth = window.innerWidth;

      let out = false;

      if (windowWidth < 768){
        out = true;
      }

      return out;
    }
  },

  created() {
    this.lock = true;
    this.isSmallWidth = this.checkWidthSize();
  },

  mounted() {
    window.onresize = () => {
      this.isSmallWidth = this.checkWidthSize();
    }
  }
}
</script>

<style scoped>

</style>